import DiaryCard from '../components/DiaryCard/DiaryCard';
import { runQuery } from '../../lib/db';

const MainPage = async () => {
  let diaries = [];
  try {
    // データベースからデータを取得し、プレーンオブジェクトに変換
    const rawDiaries = await runQuery('SELECT * FROM diary');
    console.log(rawDiaries); 

    diaries = rawDiaries.map((diary: any) => ({
      id: diary.id,
      title: diary.title,
      content: diary.content,
      created_at: diary.created_at,
    }));
  } catch (error) {
    console.error('Error fetching diaries:', error);
  }

  return (
    <div className="p-3">
      <header className="flex justify-between items-center">
        <h1 className="text-2xl font-bold flex items-center">日報</h1>
        <a href="/diary/new" className="flex items-center gap-1 font-semibold text-blue-500 hover:text-blue-700">
          new
        </a>
      </header>
      <div className="mt-8 flex flex-wrap gap-4">
      {diaries.map((diary) => (
          <DiaryCard key={diary.id} diary={diary} />
        ))}
      </div>
    </div>
  );
};

export default MainPage;
